

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Renderer2,
  Self,
  SimpleChanges
} from '@angular/core';
import { NgControl } from '@angular/forms';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { ButtonSize } from 'tds-ui/core/config';
import { InputBoolean } from 'tds-ui/shared/utility';


@Directive({
  selector: 'input[tds-input],textarea[tds-input]',
  exportAs: 'tdsinput',
  host: {
    '[class.tds-input-disabled]': 'disabled',
    '[class.tds-input-borderless]': 'tdsBorderless',
    '[attr.disabled]': 'disabled || null',
    '[class.tds-input-rtl]': `dir=== 'rtl'`,
    '[class.tds-input-sm]': `tdsSize=== 'sm'`,
    '[class.tds-input-md]': `tdsSize=== 'md'`,
    '[class.tds-input-lg]': `tdsSize=== 'lg'`,
    '[class.tds-input-xl]': `tdsSize=== 'xl'`
  }
})
export class TDSInputDirective implements OnChanges, OnInit, OnDestroy {
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_tdsBorderless: BooleanInput;
  @Input() @InputBoolean() tdsBorderless = false;
  @Input() tdsSize: ButtonSize = 'md';
  @Input()
  get disabled(): boolean {
    if (this.ngControl && this.ngControl.disabled !== null) {
      return this.ngControl.disabled;
    }
    return this._disabled;
  }
  set disabled(value: boolean) {
    this._disabled = value != null && `${value}` !== 'false';
  }
  _disabled = false;
  disabled$ = new Subject<boolean>();
  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();

  constructor(
    @Optional() @Self() public ngControl: NgControl,
    renderer: Renderer2,
    elementRef: ElementRef,
    @Optional() private directionality: Directionality
  ) {    
    renderer.addClass(elementRef.nativeElement, 'tds-input');
  }

  ngOnInit(): void {
    if (this.ngControl) {
      this.ngControl.statusChanges
        ?.pipe(
          filter(() => this.ngControl.disabled !== null),
          takeUntil(this.destroy$)
        )
        .subscribe(() => {
          this.disabled$.next(this.ngControl.disabled!);
        });
    }

    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { disabled } = changes;
    if (disabled) {
      this.disabled$.next(this.disabled);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
